 
import React, { Component, useState } from "react";
import { Stepper, Step, StepLabel, Button, Box } from '@mui/material';
import Rating from '../inc/rating';
import StepOne from '../inc/stepone';
import TextField from '@mui/material/TextField';
import Link from '@mui/material/Link'

const steps = ['Step 1', 'Step 2', 'Step 3', 'Step 4'];

const MultiStepForm = () => {
  const [activeStep, setActiveStep] = useState(0);

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };
  

  const Btns = () => {
    return (
        <div>
        <p>{steps[activeStep]}</p>
        <Button disabled={activeStep === 0} onClick={handleBack}>
          Back
        </Button>
        <Button variant="contained" color="primary" onClick={handleNext}>
          {activeStep === steps.length - 1 ? 'Finish' : 'Next'}
        </Button>
      </div>
    );
  }
  
  var sqltext = ` 
  /*!40101 SET @OLD_CHARACTER_SET_CLIENT=@@CHARACTER_SET_CLIENT */;
  /*!40101 SET @OLD_CHARACTER_SET_RESULTS=@@CHARACTER_SET_RESULTS */;
  /*!40101 SET @OLD_COLLATION_CONNECTION=@@COLLATION_CONNECTION */;
  /*!40101 SET NAMES utf8mb4 */;
   
  CREATE TABLE 'res-audio' (
    'id' int(11) NOT NULL,
    'owner' text NOT NULL,
    'url' text NOT NULL,
    'name' text NOT NULL,
    'cat' int(11) NOT NULL
  ) ENGINE=InnoDB DEFAULT CHARSET=utf8 COLLATE=utf8_general_ci;
  
   
  CREATE TABLE 'res-audio-profile' (
    'id' int(11) NOT NULL,
    'owner' text NOT NULL,
    'name' text NOT NULL,
    'url' text NOT NULL
  ) ENGINE=InnoDB DEFAULT CHARSET=utf8 COLLATE=utf8_general_ci;
  
   
  
  CREATE TABLE 'res-spotify' (
    'id' int(11) NOT NULL,
    'owner' text NOT NULL,
    'name' text NOT NULL,
    'img' text NOT NULL,
    'url' longtext NOT NULL,
    'cur_mahlas' text NOT NULL,
    'cur_avatar' text NOT NULL
  ) ENGINE=InnoDB DEFAULT CHARSET=utf8 COLLATE=utf8_general_ci;
  
   
  ALTER TABLE 'res-audio'
    ADD PRIMARY KEY ('id');
  
   
  ALTER TABLE 'res-audio-profile'
    ADD PRIMARY KEY ('id');
  
   
  ALTER TABLE 'res-spotify'
    ADD PRIMARY KEY ('id');
  
   
   
  ALTER TABLE 'res-audio'
    MODIFY 'id' int(11) NOT NULL AUTO_INCREMENT;
  
   
  ALTER TABLE 'res-audio-profile'
    MODIFY 'id' int(11) NOT NULL AUTO_INCREMENT;
  
   
  ALTER TABLE 'res-spotify'
    MODIFY 'id' int(11) NOT NULL AUTO_INCREMENT;
  COMMIT;
  
  /*!40101 SET CHARACTER_SET_CLIENT=@OLD_CHARACTER_SET_CLIENT */;
  /*!40101 SET CHARACTER_SET_RESULTS=@OLD_CHARACTER_SET_RESULTS */;
  /*!40101 SET COLLATION_CONNECTION=@OLD_COLLATION_CONNECTION */;
  `;

   
  sqltext = sqltext.replaceAll("'", "`");
 
  return (
    <div>
      <Stepper activeStep={activeStep} alternativeLabel>
        {steps.map((label) => (
          <Step key={label}>
            <StepLabel>{label}</StepLabel>
          </Step>
         
        ))}
      </Stepper>
      <div className="second-center">
        {activeStep === steps.length ? (
             <Box
             sx={{
                m: 5,       
                display: 'flex',
                flexWrap: 'wrap',
                justifyContent: 'center',
             }}
           >
          <div>
            <h3 style={{ color: '#1976d2'}}>Congratulations you have completed all the steps. You can start using your script.</h3>
            <Rating />
          </div>
          </Box>
        ) : activeStep === 0  ? (

            <div>
                <StepOne />
                <Btns />
            </div>
           
          ) : activeStep === 1  ? (
            <div>
                  <h1>Import the sql </h1>
                <TextField
          id="outlined-multiline-static3"
          label="Import the sql "
          multiline
          fullWidth 
          rows={50}
          value={sqltext}
        />
        
        <Btns />
            </div>
        ) : activeStep === 2  ? (
            <Box sx={{ '& button ': { m: 1 }  }}>
                <h1>Required Scripts will be need</h1>

              
                <Link href="https://github.com/0resmon/yarn"><Button variant="outlined">yarn</Button></Link>
                <Link href="https://github.com/0resmon/webpack"><Button variant="outlined">webpack</Button></Link>
           
                <div style={{ marginTop: "2vh" }}>
                Get download the ready node_modules file <Link href="https://github.com/0resmon/res-audio">https://github.com/0resmon/res-audio</Link>  
                
                <p>Download this file and paste it into the script folder.</p>
                <p>You can get the necessary modules by doing "npm install". but we recommend you to download the ready-made node_modules file as you may have problems.</p>
                </div>
                

             <Btns />
            </Box>
        ) : activeStep === 3 ? (
          <Box sx={{ '& button ': { m: 1 }  }}>
          <h1>Start Resources</h1>
          <p>Put the script in your source file and run it from your server.cfg file.</p>
          <TextField
          id="outlined-multiline-static3"
          label="Import the sql "
          multiline
          fullWidth 
          rows={1}
          value={`ensure res-audio`}
        />
         <p style={{color: 'red'}}>The script name should definitely be "res-audio". Otherwise the script may not work.</p>
            <Btns />
           
        </Box>
        ) : (
            <Btns />
        )}
      </div>
    </div>
  );
};


class RES_STUDIO extends Component {
 
  

  render() {
   return (
    <div className="Second" >
        <header className="second-header">
            <h1>Res Audio</h1>
        </header>
        <MultiStepForm />
    </div>  
    );
  }
}
 
export default RES_STUDIO; 