import React from 'react';
 
import Box from '@mui/material/Box';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
 
import axios from 'axios';
 



const Status = () => {
    // axios.get('https://raw.githubusercontent.com/0resmon/db/main/sales.json', {
    //     headers: {
    //         'Access-Control-Allow-Origin': '*',
    //         'Content-Type': 'application/json',
    //     }
    // })
    // .then(response => {
     
    //     console.log(response.data);
    // })
    // .catch(error => {
      
    //     console.error(error);
    // });
    const [posts, setPosts] = React.useState([]);

    React.useEffect(() => { 
        const getdata = async() => {
          const { data: res } = await axios.get("https://raw.githubusercontent.com/0resmon/db/main/script_status.json");
            console.log(res);
            setPosts(res)
         };
         getdata();
       }, []);
  return (
 
    <Box sx={{  py: 5,  px: 5, maxWidth: 600  }} >
    <p>Scripts in working process:</p>
     {posts.map((v) => (
        <Accordion>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography>{v.name}</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            {v.desc}
          </Typography>
        </AccordionDetails>
      </Accordion>
      ))}
       
      
      
      
    </Box>
  );
  
};

 


export default Status;